.thumbnail {
    cursor: pointer;
    width: 3em;
    margin: 0.5em;
}

.picture-modal {
    position: fixed;
    z-index: 100000010;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 5px 5px 20px #222;
    width: 40em;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 1em;
}

.modal-image {
    width: 100%;
}

.x-box {
    font-weight: bolder;
    z-index: 100000020;
    font-size: 1em;
    background-color: #fff;
    padding: 0.1 0.5em;
    margin: -1em -1em 0 auto;
    top: -10px;
    cursor: pointer;
    text-align: right;
}

.picture-and-arrows {
    display: flex;
    flex-direction: row;
}

.arrow {
    border: solid black;
    border-width: 0 10px 10px 0;
    display: inline-block;
    padding: 3px;
    height: 3em;
    width: 3em;
    opacity: 0.5;
    position: fixed;
    margin: auto 0;
  }

.arrow:hover {
    opacity: 1;
}
  
  .right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    right: 1em;
    top: 50%;
  }
  
  .left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    left: 1em;
    top: 50%;
  }

@media (max-width: 700px) {
    .picture-modal {
        max-width: 30em;
    }
}

@media (max-width: 530px) {
    .picture-modal {
        max-width: 25em;
    }
}